.glyph {
    padding: 0;
    margin-bottom: 20px;
    height: 100%;
    flex-wrap: wrap;
    display: flex;
}
.preview-glyphs {
    width: 168px;
    text-align: center;
}
.size-60 {
    font-size: 42px;
    width: 160px;
    text-align: center;
    display: inline-block;
    line-height: 1;
    position: relative;
}
pre {
    background-color: var(--color_grey_light);
    padding: 4px 2px;
    display: block;
    margin: 2px;
    font-size: 10.62px;
    line-height: 1.4;
    word-break: break-all;
    word-wrap: break-word;
    color: var(--color_text_dark);
    border: 1px solid #eeeeee;
    border-radius: 4px;
    font-weight: bold;
}

@media only screen and (max-width: 760px) {
    pre {
        font-size: 6px;
    }
    .preview-glyphs {
        width: 90px;
    }
    .size-60 {
        font-size: 40px;
        width: 90px;
    }
    .container {
        padding-bottom: 80px;
    }
}